.fastio-nav button.btn.dropdown-toggle {
  cursor: pointer;
  font-weight: 400;
  font-size: 1.2rem;
  color: #00aeef;
  background-color: transparent;
  text-decoration: none;
  border-color: transparent;
  box-shadow: none;
  padding: 0 5px 0 0;
  margin: 0;
